import React from 'react'
import { useSelector } from 'react-redux'
import { useState } from 'react'
import axios from 'axios'
import { useEffect } from 'react'
import {Outlet} from 'react-router-dom'
function DashBoard() {
    let {currentUser}=useSelector(state=>state.userLogin)
    let [err,setErr]=useState('')
    let [balance,setBalance]=useState([])
    const getUserBalance= async () => {
      let res= await axios.get(`http://localhost:4000/user-api/user-balance/${currentUser.username}`)
      // console.log(res.data.payload)
      if(res.data.message==='Amount'){
        setBalance(res.data.payload)
        console.log(res.data.payload);
        
      }else{
       setErr(res.data.message)
      }
     };
   
    //  const readArticleByArticleId = (articleObj) => {
    //    navigate(`../article/${articleObj.articleId}`,{state:articleObj});
    //  };
   
     useEffect(() => {
       getUserBalance();
       const total = balance.reduce((acc, obj) => acc + +obj.amount, 0)
     }, []);
  return (
    <div>
      {balance === null ? (
        <p
          className="display-1  text-center"
          style={{ color: "var(--crimson)" }}
        >
          0
        </p>
      ):(
      <p className='lead fs-3 mt-3 text-success'>Total Balance: {balance.reduce((acc, obj) => acc + +obj.amount, 0)}</p>
  )}
      // {/* <Outlet /> */}
    </div>
  )
}

export default DashBoard